import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, DebitoCreditoDarf } from '../../../../models/dtos/mfp/debitoCreditoDarf';
import { GUIA_DARF_ENUM_VALUES } from '../../../../models/enumerated/mfp/guiaDarfEnum';
import { TIPO_DEBITO_CREDITO_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoDebitoCreditoEnum';
import { MANUAL } from '../../../../models/enumerated/mlf/tipoGeracaoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface DebitoCreditoDarfEditViewProps extends EditViewProps<DebitoCreditoDarf> {}

const DebitoCreditoDarfEditView: FC<DebitoCreditoDarfEditViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<any>({ defaultValues: selectedItem, mode: 'onBlur' });

  const setIdEmpresaMFP = () => {
    const {
      empresaMFP: { id, referencia },
    } = globalReducer.globalParameter;

    setTimeout(() => {
      formProps.setValue('empresaMFP.id', id);
      formProps.setValue('referencia', referencia);
      formProps.setValue('tipoGeracao', MANUAL.value);
    }, 1000);
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        setIdEmpresaMFP();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Débito/Crédito DARF">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresaMFP.id" hidden={true} />
        <InputGroupInline formProps={formProps} id="tipoGeracao" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              placeholder="Informe a referência."
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipoDebitoCredito"
              isPrependLabel={true}
              label="Tipo de Pagamento"
              placeholder="Informe o tipo de pagamento."
              type={InputType.DROP_DOWN}
              inputDropDownProps={{ optionsProvider: TIPO_DEBITO_CREDITO_ENUM_VALUES }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipoDarf"
              isPrependLabel={true}
              label="Tipo de Darf"
              placeholder="Informe o tipo de darf."
              type={InputType.DROP_DOWN}
              inputDropDownProps={{ optionsProvider: GUIA_DARF_ENUM_VALUES }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="valor"
              isPrependLabel={true}
              label="Valor"
              placeholder="Informe o valor."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default DebitoCreditoDarfEditView;
